import { startSession } from './lib/xr/startSession.js'
import { endSession } from './lib/xr/endSession.js'
import { getCurrentPosition } from './lib/map/getCurrentPosition.js'
import { initMap } from './lib/map/initMap.js'

import cube from './data/vienna/smallCube.mjs'

const onClickStartScene = ({ arButton, map, L }) => async () => {
  console.log('locating...')

  let position = {
    lat: 48.22,
    lon: 16.38,
  }

  try {
    position = await getCurrentPosition()
  } catch (e) {
    // use default position, but error if no position could be resolved
    if (!position.alt) {
      console.error('Error locating:', e)
    }
  }

  const sessionConfig = { requiredFeatures: ['hit-test'] }

  let xrSupported = false

  try {
    xrSupported = await navigator.xr.isSessionSupported('immersive-ar')
  } catch (e) {
    // we do not care, fallback is built in.
    console.error('xrSupported error', e)
  }

  arButton.textContent = 'Start'

  let started = false
  let toggling = false

  arButton.addEventListener('click', async () => {
    toggling = true

    if (!started) {
      await startSession({ arButton, data: cube, sessionConfig, started, xrSupported })

      toggling = false
      started = true
    } else {
      await endSession({ arButton, renderer })

      started = false
      toggling = false
    }
  })
}

const main = async () => {
  const { map, L } = await initMap()

  const arButton = document.getElementById('ARButton')

  arButton.textContent = 'Locate me'

  arButton.addEventListener('click', onClickStartScene({ arButton, map, L }))
}

main()
